import offerImage2 from "../../medi.jpg";
import imageIc from "../../eyes.png";
import { createMuiTheme } from "@material-ui/core/styles";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#80d8ff",
    },
    secondary: {
      main: "#e3f2fd",
    },
  },
});

