import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import {
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Button,
  Divider,
  Typography,
  SwipeableDrawer,
} from "@material-ui/core";
import HomeIcon from "@material-ui/icons/Home";
import BubbleChartIcon from "@material-ui/icons/BubbleChart";
import InfoIcon from "@material-ui/icons/Info";
import CloseIcon from "@material-ui/icons/Close";
import VisibilityIcon from "@material-ui/icons/Visibility";

const SideBar = ({
  menuDefault,
  closeMenu,
  handleSideBarOnOpen,
  handleSideBarOnClose,
}) => {
  const useStyles = makeStyles((theme) => ({
    drawerPaper: {
      width: "inherit",
    },
    link: {
      textDecoration: "none",
      color: theme.palette.text.primary,
    },
  }));
  const classes = useStyles();

  return (
    <SwipeableDrawer
      onOpen={handleSideBarOnOpen}
      onClose={handleSideBarOnClose}
      style={
        menuDefault
          ? {
              width: "240px",
              position: "fixed",
              zIndex: "99",
              backgroundColor: "rgba(0,0,0,0.5)",
            }
          : { zIndex: "3" }
      }
      variant="persistent"
      anchor="left"
      open={menuDefault}
      classes={{ paper: classes.drawerPaper }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "flex-start",
          paddingLeft: "25px",
          paddingTop: "8%",
        }}
      >
        <Typography variant="h5" style={{ color: "#243D99" }}>
          KL
        </Typography>
        <VisibilityIcon style={{ color: "#243D99" }} />
        <Typography variant="h5" style={{ color: "#243D99" }}>
          P
        </Typography>
      </div>
      <Divider light />
      <Link to="/" className={classes.link}>
        <List>
          <ListItem button>
            <ListItemIcon>
              <HomeIcon />
            </ListItemIcon>
            <ListItemText primary={"Home"} />
          </ListItem>
        </List>
      </Link>

      <Link to="/analyse" className={classes.link}>
        <List>
          <ListItem button>
            <ListItemIcon>
              <BubbleChartIcon />
            </ListItemIcon>
            <ListItemText primary={"Analyse"} />
          </ListItem>
        </List>
      </Link>

      <Link to="/about" className={classes.link}>
        <List>
          <ListItem button>
            <ListItemIcon>
              <InfoIcon />
            </ListItemIcon>
            <ListItemText primary={"About Us"} />
          </ListItem>
        </List>
      </Link>
      <Button onClick={closeMenu} style={{}}>
        <CloseIcon style={{ color: "red" }} />
      </Button>
    </SwipeableDrawer>
  );
};

export default SideBar;
