import React, { useEffect } from "react";
import ReactLoading from "react-loading";
import AnaImage from "../../med.svg";
import "../../fonts/MuseoModerno-VariableFont_wght.ttf";
import "dotenv";
import {
  Container,
  Typography,
  Divider,
  Paper,
  TextField,
  Button,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Snackbar,
  IconButton,
} from "@material-ui/core";
import DoneAllIcon from "@material-ui/icons/DoneAll";
import DeviceHubIcon from "@material-ui/icons/DeviceHub";
import CloseIcon from "@material-ui/icons/Close";

const AnalysisPage = ({
  imageUrlHandler,
  imageUrl,
  disableDiagnoseBtn,
  handleDetect,
  diagnosisState,
  confidenceState,
  isLoading,
  openFailedRequestState,
  handleFailedRequestOnClose,
}) => {
  useEffect(() => {
    console.log("component just mountede");
  }, []);
  return (
    <div style={{ width: "100%", backgroundColor: "#632CED" }}>
      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "center",
          // height: "200px",
          paddingTop: "30px",
          paddingBottom: "50px",
          width: "100%",
          backgroundColor: "#632CED",
          height: "auto",
          //   borderRadius: " 0px 0px 100px 100px",
        }}
      >
        <div
          style={{
            color: "white",
            fontSize: "10px",
            width: "100%",
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "center",
            paddingTop: "0px",
            paddingBottom: "15px",
          }}
        >
          <Typography
            variant="h5"
            style={{
              textAlign: "center",
              width: "100%",
              fontFamily: "Monospace",
              marginTop: "2%",
              fontFamily: "MuseoModerno, cursive",
            }}
          >
            AI Quick Check Medical Images
          </Typography>
          {/* <Typography
            style={{ textAlign: "center", fontSize: "15px", width: "100%" }}
          >
            Enter the link to the medical Image to be examined
          </Typography> */}
          {imageUrl === "" ? (
            <img
              // src="https://prod-images-static.radiopaedia.org/images/52140940/092a320812722a9fdaa4aeb645868f_jumbo.jpeg"
              src={AnaImage}
              style={{
                width: "20%",
                height: "auto",
                borderRadius: "10px",
                marginTop: "10px",
              }}
            />
          ) : (
            <img
              // src="https://prod-images-static.radiopaedia.org/images/52140940/092a320812722a9fdaa4aeb645868f_jumbo.jpeg"
              src={imageUrl}
              style={{
                width: "20%",
                height: "auto",
                borderRadius: "10px",
                marginTop: "10px",
              }}
            />
          )}
        </div>
        <div
          style={{
            backgroundColor: "white",
            display: "flex",
            flexWrap: "wrap",
            width: "80%",
            justifyContent: "center",
            borderRadius: "10px",
            padding: "10px",
          }}
        >
          <TextField
            color="primary"
            id="standard-basic"
            label="Medical Image Url"
            // variant="filled"
            style={{ width: "80%" }}
            onChange={imageUrlHandler}
          />
          <Button
            // style={{ width: "20%", color: "white" }}
            disabled={disableDiagnoseBtn}
            color="primary"
            onClick={handleDetect}
          >
            Diagnose
          </Button>
        </div>
        {confidenceState !== null && diagnosisState !== null ? (
          <div
            style={{
              display: "flex",
              flexWrap: "wrap",
              margin: "30px",
              //   width: "100%",
              paddingTop: "20px",
              paddingBottom: "20px",
              //   paddingLeft: "20%",
              //   paddingRight: "20%",
              justifyContent: "center",
              backgroundColor: "white",
              borderRadius: "50px",
              // flexWrap: "wrap",
            }}
          >
            <List style={{ borderRadius: "10px", boxShadow: "2px" }}>
              <ListItem>
                <ListItemAvatar>
                  <Avatar style={{ backgroundColor: "green" }}>
                    <DeviceHubIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary="Diagnosis" secondary={diagnosisState} />
              </ListItem>
              <ListItem>
                <ListItemAvatar>
                  <Avatar style={{ backgroundColor: "green" }}>
                    <DoneAllIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary="Confidence"
                  secondary={confidenceState}
                />
              </ListItem>

              {/* <ListItem>
                <ListItemAvatar>
                  <Avatar>
                    <BeachAccessIcon />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary="Vacation" secondary="July 20, 2014" />
              </ListItem> */}
            </List>
          </div>
        ) : isLoading ? (
          <div
            style={{ width: "100%", display: "flex", justifyContent: "center" }}
          >
            <ReactLoading type="bars" color="#ffffff" />
          </div>
        ) : (
          <div></div>
        )}
      </div>
      <Snackbar
        open={openFailedRequestState}
        autoHideDuration={3000}
        onClose={handleFailedRequestOnClose}
        message="Check internet connection!"
        action={
          <React.Fragment>
            <Button
              color="secondary"
              size="small"
              onClick={handleFailedRequestOnClose}
            >
              CLOSE
            </Button>
            <IconButton
              aria-label="close"
              color="inherit"
              onClick={handleFailedRequestOnClose}
            >
              <CloseIcon />
            </IconButton>
          </React.Fragment>
        }
      ></Snackbar>
    </div>
  );
};

export default AnalysisPage;
