import React from "react";
import { Typography } from "@material-ui/core";

const TeamCard = ({ avatar, names, skillsAndPosition }) => {
  return (
    <div
      style={{
        width: "400px",
        marginTop: "20px",
        padding: "20px",
        display: "flex",

        flexWrap: "wrap",
        justifyContent: "space-around",
        // width: "80%",
        // backgroundColor: "#632CED",
        // borderInlineColor: "#632CED",
        // borderBlock: "solid",
        // border: "solid 2px #632CED",
        // borderRadius: "10px",
        // boxShadow: "0px 1px 1px 1px rgba(0,0,0,0.15)",
        // boxShadow: "3px 6px grey",
      }}
    >
      <div
        style={{
          borderRadius: "10px",
          // backgroundColor: "red",
          padding: "20px",
          width: "100%",
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "space-around",
          marginBottom: "3%",
          boxShadow: "0px 1px 1px 1px rgba(0,0,0,0.15)",
        }}
      >
        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "space-around",
          }}
        >
          <img
            src={avatar}
            style={{
              display: "flex",
              flexWrap: "wrap",
              width: "150px",
              height: "200px",
              borderRadius: "80px",
            }}
          />
        </div>
        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            flexDirection: "column",
          }}
        >
          <Typography variant="h6">{names}</Typography>
          <Typography variant="subtitle1">{skillsAndPosition}</Typography>
        </div>
      </div>
    </div>
  );
};

export default TeamCard;
