import React, { useEffect } from "react";
import { Switch, Route } from "react-router-dom";
import AnalysisPage from "../analysisPage/AnalysisPage";
import HomePage from "../homepage/HomePage";
import AboutUsPagee from "../aboutUs/AboutUsPage";
import { Container, Button, TextField, Typography } from "@material-ui/core";
import ReactLoading from "react-loading";

const SwitchRoute = ({
  imageUrlHandler,
  imageUrl,
  disableDiagnoseBtn,
  handleDetect,
  diagnosisState,
  confidenceState,
  isLoading,
  openFailedRequestState,
  handleFailedRequestState,
}) => {
  return (
    <Switch>
      <Route exact path="/">
        <HomePage />
      </Route>
      <Route exact path="/analyse">
        <AnalysisPage
          imageUrl={imageUrl}
          imageUrlHandler={imageUrlHandler}
          disableDiagnoseBtn={disableDiagnoseBtn}
          handleDetect={handleDetect}
          diagnosisState={diagnosisState}
          confidenceState={confidenceState}
          isLoading={isLoading}
          openFailedRequestState={openFailedRequestState}
          handleFailedRequestOnClose={handleFailedRequestState}
        />
      </Route>
      <Route exact path="/about">
        <Container>
          <AboutUsPagee />
        </Container>
      </Route>
    </Switch>
  );
};

export default SwitchRoute;
